import React from 'react';
import './index.scss';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Carousel, { Dots } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';
import { useState } from 'react';


import c1 from '../../assets/images/PIT/IMG1.png';
import c2 from '../../assets/images/PIT/IMG2.png';
import c3 from '../../assets/images/PIT/IMG3.png';
import c4 from '../../assets/images/PIT/IMG4.png';
import c5 from '../../assets/images/PIT/IMG5.png';
import c6 from '../../assets/images/PIT/IMG6.png';

const useStyles = makeStyles({
    root: {
        padding: '6rem 0',
    }
});


class PITCarousel extends React.Component {
    constructor() {
        super()
        this.state = {
            value: 0,
            slides: [
                (<img src={c1} className="img-example" />),
                (<img src={c2} className="img-example" />),
                (<img src={c3} className="img-example" />),
                (<img src={c4} className="img-example" />),
                (<img src={c5} className="img-example" />),
                (<img src={c6} className="img-example" />),
            ],
        }
        this.onChange = this.onChange.bind(this);
    }

    onChange(value) {
        this.setState({ value });
    }

    render() {
        return (
            <div>
                <Carousel
                    value={this.state.value}
                    onChange={this.onChange}
                    slides={this.state.slides}
                    plugins={[
                        'arrows',
                        'clickToChange'
                    ]}
                />
                <Dots value={this.state.value} onChange={this.onChange} number={this.state.slides.length}
                    thumbnails={[
                    (<img key={1} className="img-example-small" src={c1} />),
                    (<img key={2} className="img-example-small" src={c2} />),
                    (<img key={3} className="img-example-small" src={c3} />),
                    (<img key={4} className="img-example-small" src={c4} />),
                    (<img key={5} className="img-example-small" src={c5} />),
                    (<img key={6} className="img-example-small" src={c6} />),
                ]}
                />
            </div>
        );
    }
}

export default PITCarousel;